"use client";

import { useState, useEffect } from 'react';

export interface WeatherData {
  location: {
    name: string;
    country: string;
  };
  current: {
    temp_c: number;
    feelslike_c: number;
    condition: {
      text: string;
      icon: string;
    };
    humidity: number;
    wind_kph: number;
    pressure_mb: number;
  };
}

interface WeatherProps {
  onDataLoaded: (data: WeatherData) => void;
}

const FetchWeather = ({ onDataLoaded }: WeatherProps) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchWeather = async () => {
      try {
        const API_KEY = '7f9080f430964821b52103358250804';
        const response = await fetch(
          `https://api.weatherapi.com/v1/current.json?key=${API_KEY}&q=Saint-Petersburg&lang=ru`
        );

        if (!response.ok) throw new Error('Ошибка получения данных');

        const data: WeatherData = await response.json();
        onDataLoaded(data);
        setError(null);
      } catch (err) {
        setError('Не удалось получить данные о погоде');
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchWeather();
  }, [onDataLoaded]);

  if (loading) return <div>Загрузка...</div>;
  if (error) return <div>{error}</div>;
  return null;
};

export default FetchWeather;